<template>
  <div class="whole-container">
    <div class="form-container">
      <h2>토핑 데이터 추가</h2>
      <form id="cafeForm">
        <div class="form-group">
          <label for="toppingName">카페 이름</label>
          <input
            type="text"
            id="cafeName"
            placeholder="ex. 스타벅스"
            required
            v-model="saveToppingsDto.cafeName"
          />
        </div>
        <div class="form-group">
          <label for="beverageName">음료 이름</label>
          <input
            type="text"
            id="toppingName"
            placeholder="ex. 아이스 아메리카노"
            required
            v-model="saveToppingsDto.beverageName"
          />
        </div>
        <div class="form-group">
          <label for="toppingName">토핑 이름</label>
          <input
            type="text"
            id="toppingName"
            placeholder="ex. 휘핑크림"
            required
            v-model="saveToppingsDto.toppingName"
          />
        </div>

        <div class="form-group">
          <label for="kcal">칼로리 (숫자만 입력)</label>
          <input
            type="number"
            id="kcal"
            placeholder="ex. 165"
            required
            v-model="saveToppingsDto.calorie"
          />
        </div>

        <div class="form-group">
          <label for="sugar">당류 (숫자만 입력)</label>
          <input
            type="number"
            id="sugar"
            placeholder="ex. 30"
            required
            v-model="saveToppingsDto.sugar"
          />
        </div>

        <div class="form-group">
          <label for="manager">담당자</label>
          <input
            type="text"
            id="manager"
            placeholder="ex. 홍길동"
            required
            v-model="saveToppingsDto.manager"
          />
        </div>

        <button type="button" @click="submitForm()">완료</button>
      </form>
    </div>
    <div class="list-container">
      <table class="cafe-list-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>토핑 이름</th>
            <th>당류</th>
            <th>칼로리</th>
            <th>삭제 여부</th>
            <th>카페 이름</th>
            <th>음료 이름</th>
            <th>담당자</th>
            <th>삭제 전환</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(toppings, idx) in storedToppings" :key="toppings">
            <th v-for="topping in toppings" :key="topping">
              {{ topping }}
            </th>
            <button
              class="delete-button"
              @click="deleteTopping(storedToppings[idx].id)"
            >
              삭제 토글
            </button>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      storedToppings: [
        {
          toppingId: 0,
          cafeName: "",
          beverageName: "",
          toppingName: "test",
          sugar: 1,
          calorie: 2,
          deleted: false,
          manager: "tm",
        },
      ],
      saveToppingsDto: {
        cafeName: "",
        beverageName: "",
        toppingName: "",
        sugar: "",
        calorie: "",
        deleted: false,
        manager: "",
      },
    };
  },
  components: {},
  mounted() {
    this.loadContents();
  },
  methods: {
    loadContents() {
      this.$axios.get("/admin/topping").then((res) => {
        this.storedToppings = res.data;
        console.log(res.data);
      });
    },
    submitForm() {
      this.$axios
        .post("/admin/topping/save", this.saveToppingsDto)
        .then((res) => {
          console.log(res);
          this.loadContents();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    deleteTopping(id) {
      console.log(id);
      this.$axios
        .delete("/admin/topping/delete/" + id)
        .then(() => {
          this.loadContents();
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style>
@import "../assets/styles.css";
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
