<template>
  <div class="whole-container">
    <div class="form-container">
      <h2>카페 데이터 추가</h2>
      <form id="cafeForm">
        <div class="form-group">
          <label for="cafeName">카페명</label>
          <input
            type="text"
            id="cafeName"
            placeholder="ex. 투썸플레이스"
            required
            v-model="saveCafes.cafeName"
          />
        </div>

        <div class="form-group">
          <label for="drinkName">음료 이름</label>
          <input
            type="text"
            id="drinkName"
            placeholder="ex. 딸기 라떼"
            required
            v-model="saveCafes.beverageName"
          />
        </div>

        <div class="form-group">
          <label>온도</label>
          <div class="radio-group">
            <input
              type="radio"
              id="hot"
              name="temperature"
              value="true"
              v-model="saveCafes.temperature"
              checked
            />
            <label for="hot">HOT</label>
            <input
              type="radio"
              id="ice"
              name="temperature"
              value="false"
              v-model="saveCafes.temperature"
            />
            <label for="ice">ICE</label>
          </div>
        </div>
        <br />

        <div class="form-group">
          <label for="kcal">칼로리 (숫자만 입력)</label>
          <input
            type="number"
            id="kcal"
            placeholder="ex. 165"
            required
            v-model="saveCafes.calorie"
          />
        </div>

        <div class="form-group">
          <label for="sugar">당류 (숫자만 입력)</label>
          <input
            type="number"
            id="sugar"
            placeholder="ex. 30"
            required
            v-model="saveCafes.sugar"
          />
        </div>
        <div class="form-group">
          <label for="serve">용량 (숫자만 입력)</label>
          <input
            type="number"
            id="serve"
            placeholder="ex. 355"
            required
            v-model="saveCafes.serve"
          />
        </div>

        <div class="form-group">
          <label for="size">사이즈</label>
          <input
            type="text"
            id="size"
            placeholder="ex. tall"
            required
            v-model="saveCafes.size"
          />
        </div>

        <div class="form-group">
          <label for="manager">담당자</label>
          <input
            type="text"
            id="manager"
            placeholder="ex. 홍길동"
            required
            v-model="saveCafes.owner"
          />
        </div>

        <button type="button" @click="submitForm()">완료</button>
      </form>
    </div>
    <div class="list-container">
      <table class="cafe-list-table">
        <thead>
          <tr>
            <th>ID</th>
            <th>카페명</th>
            <th>음료명</th>
            <th>사이즈</th>
            <th>제공량(ml)</th>
            <th>당류</th>
            <th>칼로리</th>
            <th>온도(true = hot, false = ice)</th>
            <th>삭제 여부</th>
            <th>담당자</th>
            <th>삭제 전환</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(cafes, idx) in storedCafes" :key="cafes">
            <th v-for="cafe in cafes" :key="cafe">
              {{ cafe }}
            </th>
            <button
              class="delete-button"
              @click="deleteCafe(storedCafes[idx].id)"
            >
              삭제
            </button>
          </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
export default {
  name: "App",
  data() {
    return {
      storedCafes: [
        {
          cafeId: 0,
          beverageName: "",
          cafeName: "",
          calorie: 0,
          serve: 0,
          size: "",
          suger: 0,
          temperature: true,
          deleted: true,
          manager: "",
        },
      ],
      saveCafes: {
        beverageName: "",
        cafeName: "",
        temperature: true,
        calorie: "",
        sugar: "",
        size: "",
        manager: "",
        topping: "",
        deleted: false,
      },
    };
  },
  components: {},
  mounted() {
    this.loadContents();
  },
  methods: {
    loadContents() {
      this.$axios.get("/admin/cafe").then((res) => {
        this.storedCafes = res.data;
        console.log(res.data);
      });
    },
    submitForm() {
      this.$axios
        .post("/admin/cafe/save", this.saveCafes)
        .then((res) => {
          console.log(res);
          this.loadContents();
        })
        .catch((err) => {
          console.log(err);
        });
    },
    deleteCafe(id) {
      this.$axios
        .delete("/admin/cafe/delete/" + id)
        .then(() => {
          this.loadContents();
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

